.onboarding-aside {
    background-color: var(--bs-body-bg);
    height: calc(100vh - 53px);
    overflow: auto;
    // box-shadow: 0px 10px 10px #ccc;
    .step-progress .progress{
        height: 8px;
    }
    .progress .progress-bar{
        background-color: #6AC259;
        border-radius: 5px;
    }
}

.clearfix-chat {
    margin-left: 30px;
}
.fade-item {
    transition: 2s all ease-in-out;
    opacity: 0;
}

.fade-item-1 {
    transition: 2s all ease-in-out;
    opacity: 0;
}

.fadein {
    animation: fadeInUp 1s;
    animation-fill-mode: forwards;
}

@keyframes fadeInUp {
    from {
        transform: translate3d(0, 20px, 0)
    }

    to {
        transform: translate3d(0, 0, 0);
        opacity: 1
    }
}

.selectSource-list {
    width: 90%;
    float: left;
    min-height: 40px;
}

.selectSource-list p {
    background-color: var(--bs-gray-200);
    padding: 10px;
    border-radius: 0px 10px 10px 10px;
    font-weight: 500;
    margin-bottom: 10px;
}
.onbording_toggle_icon {
    align-items: center;
    background-color: var(--bs-white) !important;
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
    box-shadow: 4px 5px 5px -5px rgba(0, 0, 0, .2);
    display: flex;
    height: 40px;
    justify-content: center;
    padding: 10px;
    position: absolute;
    right: -40px;
    top: 30px;
    width: 46px;
    z-index: 99;
    cursor: pointer;
    display: none;
    text-align: center;
}

.onboarding-aside-main.active .onbording_toggle_icon svg {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
}

.addfeed-modal-update .w-20 {
    width: 410px;
    position: fixed;
    left: 0px;
    z-index: 99;
}
.networkList {
    max-height: 200px;
    overflow: hidden;
    padding: 0;
    transition: all .5s;
    margin-bottom: 0px;
    li .new-tag{
        top: 2px;
        right: 1px;
        font-size: 8px;
    }
}

.selectSource-right{
    .hastag {
        left: 0px;
        font-size: 22px;
        background-color: var(--bs-gray-200);
        width: 50px;
        top: 0px;
        border-radius: 5px 0px 0px 5px;
        border: 1px solid var(--bs-border-color);
        border-right: 0px;
        justify-content: center;
        i{
            font-size: 1rem;
            color: var(--bs-gray);
        }
    }
    .custom-input input{
        border-radius: 0px 3px 3px 0px;
    }
}
.selectSource-list ul li {
    background-color: var(--bs-gray-200);
    padding: 13px 15px;
    border-radius: 5px;
    text-align: center;
    margin-right: 5px;
    margin-bottom: 5px;
    width: 90px;
    border: 1px solid var(--bs-gray-200);
    font-weight: 500;
    overflow: hidden;
    span {
        line-height: 18px;
    }
    &.filter-options{
        // background-color: var(--bs-white);
        // border: 1px solid var(--bs-dark-active);
        &:hover, &.active{
            background: var(--bs-dark-active) !important;
            color: var(--bs-white) !important;
            border-color: var(--bs-dark-active) !important;
        }
    }
}
.btn.btn-outline-gray {
    color: #2b2b2b;
    background-color: transparent;
    border-color: var(--bs-gray-200);
}
.addfeed-modal-update .w-80 {
    padding-left: 420px;
}

.text-link {
    color: var(--bs-blue);
    text-decoration: none;
    font-weight: 500;
    border: none;
    background-color: transparent;
}

.selectSource-list .fade-item,
.selectSource-right .fade-item-1,
.fade-item-1 {
    display: none;
}

.selectSource-list .fade-item.fadein,
.selectSource-right .fade-item-1.fadein,
.fade-item-1.fadein {
    display: inline-flex;
}
.selectSource-right {
    text-align: right;
    width: 90%;
    float: right;
    a {
        color: #1B74E4;
        text-decoration: underline;
    }
    p {
        background-color: var(--bs-body-bg);
        padding: 10px;
        border-radius: 10px 10px 0px 10px;
        display: inline-block;
        box-shadow: 0px 3px 6px #00000014;
        min-width: 80px;
        font-weight: 500;
        text-align: left;
    }
}
[data-theme=dark] .selectSource-right p{
    box-shadow: 0px 1px 3px rgba(255, 255, 255, 0.1);
}
.click-next{
    background-color: var(--bs-gray-200);
    font-weight: 500;
}
.symbol.symbol-25>img {
    width: 100%;
    max-width: 25px;
    height: 25px;
}

.advance-input .input-group input {
    padding: 1rem !important;
}

.advance-input .scrollbar-container {
    max-height: 160px;
    a {
        text-decoration: none;
        color: #181c32;
    }
}

.advance-input .dropdown-menu {
    .img-card {
        width: 35px;
        height: 35px;
        margin-right: 10px;
        border-radius: 3px;
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    ul li {
        height: auto;
        padding: 5px 12px;
        &:last-child{
            border-bottom: 0px;
        }
        .img-text {
            width: 83%;
            word-wrap: break-word;
        }
    }
}

.advance-input .dropdown-menu ul li .img-text>span:first-child {
    font-weight: 500;
    font-size: 13px;
}

.advance-input .dropdown-menu ul li .img-text .yt-playlist {
    color: var(--bs-secondary-color);
    font-size: 10px;
    font-weight: 400;
    margin: 0;
    padding: 0;
    display: block;
}
.handlelist ul li {
    padding: 8px 12px;
    cursor: pointer;
    color: var(--bs-heading-color);
    border-bottom: 1px solid var(--bs-border-color);
    height: 54px;
    .img-section {
        display: inline-block;
        float: left;
        img {
            border-radius: 100%;
            height: 40px;
            width: 40px;
        }
    }
}

.handlelist ul li .content-section {
    display: inline-block;
    padding: 0 10px;
    span {
        font-weight: 600;
    }    
    p {
        font-weight: 400;
        color: var(--bs-secondary-color);
        font-size: 12px;
        padding: 0;
        margin: 0;
    }
}
.handlelist {
    position: absolute;
    display: inline-block;
    top: 45px;
    background: var(--bs-body-bg);
    width: 100%;
    z-index: 99;
    border: 1px solid var(--bs-border-color);
    border-radius: 5px;
    display: none;
    max-height: 315px;
    overflow-y: auto;
}

#handlelist1 {
    height: auto;
    max-height: 218px;
    overflow-y: scroll;
}
.selectSource-list .list-custom li{
    width: 48%;
    &:first-child {
        width: 100%;
    }
}
.onboarding-main .theme_preview{
    height: auto;
    margin: 0 -12.5% 0;
}
.onboarding-main iframe {
    height: calc(100vh - 100px);
}
.onboarding-main div.noPostContent.hide_post_strip {
    width: calc(100% - 404px);
}
.noPostContent.hide_post_strip {
    position: absolute;
    bottom: 0;
    width: 100%;
    z-index: 94;
    background-color: var(--bs-gray-200);
    left: 0;
    right: 0;
    max-width: 100%;
    left: inherit;
    p{
        color: var(--bd-light);
    }
}
.selectSource-list ul li span{
    // color: var(--bs-body-color);
}
.preview-main{
    height: calc(100vh - 50px);
}
@media screen and (max-width: 1600px) {
    .networkList {
        max-height: 175px;
        ul li{
            padding: 6px;
            width: 74px;
        }
    }
    .addfeed-modal-update .w-20 {
        width: 360px;
    }
    .addfeed-modal-update .w-80 {
        padding-left: 370px;
    }
    .onboarding-main div.noPostContent.hide_post_strip {
        width: calc(100% - 360px);
    }
    .selectSource-list ul li{
        padding: 6px;
        width: 74px;
    }
}

@media screen and (max-width: 767px) {
    .onboarding-main div.noPostContent.hide_post_strip {
        width: calc(100% - 320px);
    }
}